import React from 'react'

interface Product {
  id: string,
  name: string,
  cover: string,
  stars: number,
  price: number,
  sales: number,
}
  
const Products = ({products}: any) => {
  return (
    <div className="container">
      <div className="products">
        {products.map((product: Product) => (
          <div key={product.id} className="products-card">
            <div className="img-product"><img src={product.cover} alt={product.name} /></div>
            <div className="ctn-card">
              <h5 className="products-title">{product.name}</h5>
                <p className="star">vote: {product.stars}</p>
              <div className="info">
                <p className="old-price">?</p>
                <p className="price">{product.price}$</p>
                <p className="sales">{product.sales}</p>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  )
};

export default Products